.searchpart {
  width: "100%";
  position: "relative";
  top: 240px;
  z-index: 7890657890;
  padding: 20px;
}

.video-container {
  position: relative;
  overflow-y: scroll;
  margin-top: 250px;
  height: 80vh;
}

.page-title {
  font-weight: bolder !important;
  color: #10a3ff !important;
  text-align: left;
  margin: 0;
  padding: 0;
  letter-spacing: -2px !important;
  font-family: $worksans-extrabold !important;
}

.video-title h2 {
  font-weight: bolder;
  color: #5e5e5e;
}

.video-description h5 {
  line-height: 2;
  font-size: 12px;
}

.video-item h5 {
  font-weight: bolder;
  margin-top: 5px;
  color: #4d4d4d;
  line-height: 1.5;
  font-family: "work sans" !important;
}

.short-desc {
  font-size: 12px;
}

.videoList-container {
  height: 80vh;
  overflow-x: hidden;
  margin-top: 250px;
  overflow-y: scroll;
}

.videoList-container {
  .video-item {
    margin-bottom: 1.5em;

    &:hover {
      cursor: pointer;
    }
  }
}

.videoList-container > .video-item img {
  position: relative;
}

.tabs {
  width: 100%;
  display: block;
  margin: 10px;
  position: relative;
}

.tabs .tab {
  float: left;
  display: block;
}

.tabs .tab > input[type="radio"] {
  position: absolute;
  top: -9999px;
  left: -9999px;
}

.tabs .tab > label {
  display: inline-block;
  padding: 6px 6px;
  text-transform: uppercase;
  cursor: pointer;
  position: relative;
  color: rgba(7, 1, 1, 0.568);
  font-weight: bolder !important;
  font-size: 14px;
}

.tabs .content .inn-content {
  width: intrinsic; /* Safari/WebKit uses a non-standard name */
  width: -moz-max-content; /* Firefox/Gecko */
  width: -webkit-max-content; /* Chrome */
  width: max-content;
}

.tabs .content {
  z-index: 0; /* or display: none; */
  overflow-x: auto;
  overflow-y: auto;
  width: 100%;
  // background-color: red;
  padding: 25px 0;
  position: absolute;
  top: 27px;
  left: 0;

  opacity: 0;
  transition: opacity 400ms ease-out;
}

.tabs > .tab > [id^="tab"]:checked + label {
  top: 0;
  background: transparent;
  color: #0c0c0c;
  font-family: $bold-sans;
}

.tabs > .tab > [id^="tab"]:checked ~ [id^="tab-content"] {
  z-index: 1; /* or display: block; */

  opacity: 1;
  transition: opacity 400ms ease-out;
}
@media only screen and (max-device-width: 450px) {
  .video-section {
    padding: 20px 0;
    .row {
      margin: auto;
      width: 100%;
    }
  }

  .video-section1 {
    padding: 20px 0;
