    margin-top: 50px;
    height: max-content;
  }

  .page-title {
    font-size: 20px;
  }

  .tabs .tab > label {
    display: inline-block;
    padding: 5;
    font-size: 10px;
    font-weight: bold;
    text-transform: capitalize;
    color: rgba(7, 1, 1, 0.568);
  }
}
